<template>
  <APlayer id="music" :music="musicList[0]" :list="musicList" :listFolded="true" listMaxHeight="340px"></APlayer>
</template>

<script>
  import APlayer from 'vue-aplayer'
  // import response from '@/playlist'

  export default {
    name: "Footer",
    components: {
      APlayer
    },
    computed: {
      musicList: function () {
        return this.$store.state.playlist;
      }
    },
    async created() {
      let response = await fetch('http://47.102.127.209:84/music/index.json');
      response = await response.json();
      let playlist = response.map(e => {
        return {title: e[2], artist: e[1], src: 'http://47.102.127.209:84/music/' + e[0] + '.mp3'}
      });
      console.log(playlist);
      this.$store.commit('setPlaylist', playlist);
      this.$store.commit('setAllPlaylist', playlist);
    }
  }
</script>

<style scoped lang="sass">

  #music
    position: fixed
    bottom: 0
    width: 100%
    margin: 0


</style>



