<template>
  <div id="app">
    <Layout id="main">
      <Header id="header">
        <Menu mode="horizontal" theme="primary">
          <MenuItem name="index" to="/">首页</MenuItem>
          <MenuItem name="diary" to="/diary/">日记</MenuItem>
          <MenuItem name="timetable" to="/timetable/">日程</MenuItem>
          <MenuItem name="music" to="/music/">音乐</MenuItem>
        </Menu>
      </Header>
      <Content id="content">
        <router-view></router-view>
      </Content>
      <Footer id="footer">欢迎访问大潘的网站。</Footer>
    </Layout>
    <Music id="music"></Music>
  </div>
</template>

<script>
  import Music from '@/components/Music'

  export default {
    name: 'app',
    components: {
      Music,
    }
  }
</script>

<style lang="sass" scoped>
  @import "components/palette"

  #header
    background: $palette-primary

  #content
    margin: 1rem

  #main
    margin-bottom: 66px

  #footer
    background: $palette-sub-color
    color: white
</style>



