import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: () => import('@/pages/Index')
    },
    {
      path: '/diary/',
      name: 'Diary',
      component: () => import('@/pages/Diary'),
    },
    {
      path: '/timetable/',
      name: 'Timetable',
      component: () => import('@/pages/Timetable'),
    },
    {
      path: '/music/',
      name: 'Music',
      component: () => import('@/pages/Music'),
    }
  ]
})



