import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    playlist: [{title: '加载中', artist: '加载中', src: 'http://47.102.127.209:84/music/1.mp3'}],
    allPlaylist: [{title: '加载中', artist: '加载中', src: 'http://47.102.127.209:84/music/1.mp3'}],
  },
  mutations: {
    setPlaylist(state, playlist) {
      state.playlist = playlist;
    },
    setAllPlaylist(state, playlist) {
      state.allPlaylist = playlist;
    }
  }
});

export default store;



